<template>
	<view class="mine-wrapper">
		<x-tips ref="myTip" />
		<view class="bg"></view>
		<view class="mine" v-if="hasLogin == '-1'">
			<image class="logo" src="../../static/logo5.png" mode="aspectFit"></image>
			<view class="title">写寄语</view>
			<view class="decs">写诗、写实、写史、写事</view>
			<x-login title="微信一键登录" style="margin-top: 48rpx;" @submit="handleSubmit" />
		</view>
		<view class="center" v-if="hasLogin == '1'">
			<view class="center-avatar">
				<image :src="userInfo.avatarUrl || base64Avatar" mode="aspectFill"></image>
				<view class="avatar-name">
					<text>{{ userInfo.nickName || '路人甲' }}</text>
					<text>普通用户</text>
				</view>
			</view>
			<view class="top-card">
				<view class="top">
					<text>寄语：{{ total }}</text>
					<text>积分：{{ integral }}</text>
				</view>

				<view class="bom">
					<view class="bom-item" @click="handleFav">
						<uni-icons type="star" color="#606266" size="20"></uni-icons>
						<text>收藏小程序</text>
					</view>
					<button class="bom-item btn-reset" data-name="shareBtn" open-type="share">
						<uni-icons type="redo" color="#606266" size="20"></uni-icons>
						<text>好友分享</text>
					</button>
					<view class="bom-item" @click="handleRules">
						<uni-icons type="settings" color="#606266" size="20"></uni-icons>
						<text>积分规则</text>
					</view>
				</view>
			</view>
			<view class="" style="padding-bottom: 100rpx;" v-if="hasData">
				<view class="m-list" v-for="(item, index) in payload" :key="index">
					<x-card :showAvatar="false" :showAction='true' :subIndex="index" :showReport="false" @reload="handleReload" :dataSource="item" />
				</view>
			</view>
			<x-empty v-if="!hasData" />
		</view>
	</view>
</template>

<script>
import xCard from '@/components/xCard.vue';
import xEmpty from '@/components/xEmpty.vue';
import xLogin from '@/components/xLogin.vue';
import xTips from '@/components/xTips.vue';
import share from '@/static/images/share.jpg';

let base64Avatar =
	'data:image/jpg;base64,';
export default {
	components: {
		xCard,
		xEmpty,
		xLogin,
		xTips
	},
	data() {
		return {
			hasLogin: uni.getStorageSync('hasLogin') || null,
			base64Avatar
		};
	},
	props: {
		payload: {
			type: Array,
			default: () => []
		},
		userInfo: {
			type: Object,
			default: () => {}
		},
		total: {
			type: Number,
			default: 0
		}
	},
	computed: {
		hasData() {
			return this.payload.length !== 0;
		},
		integral() {
			return Math.ceil(this.total * 0.58936);
		}
	},

	methods: {
		async handleSubmit() {
			await this.getUser();
		},
		handleFav() {
			this.$refs.myTip.tipVisible = true;
			setTimeout(() => {
				this.$refs.myTip.tipVisible = false;
			}, 5000);
			console.log(this.$refs.myTip);
		},
		handleRules() {
			uni.showModal({
				showCancel: false,
				confirmText: '知道了',
				title: '积分规则',
				content: '积分计算规则根据寄语条数、寄语系数、寄语点赞数、寄语举报数、用户活跃度综合计算打分。'
			});
		},
		handleReload(page) {
			this.$emit('u-reload', page);
		}
	}
};
</script>

<style lang="scss" scoped>
.flex {
	display: flex;
	justify-content: center;
	align-items: center;
}
.mine-wrapper {
	position: relative;
	background-color: #f5f5f5;
	height: auto;
	.bg {
		position: fixed;
		background: url('../../static/images/3.webp') no-repeat;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		z-index: 0;
	}
	.center {
		position: absolute;
		min-height: calc(100vh - 100rpx);
		padding: 50rpx 15rpx 15rpx;
		width: 100%;
		box-sizing: border-box;
		z-index: 3;
		&-avatar {
			@extend .flex;
			justify-content: flex-start;
			width: 100%;
			margin-bottom: 50rpx;
			image {
				width: 140rpx;
				height: 140rpx;
				border-radius: 50%;
			}
			.avatar-name {
				@extend .flex;
				flex-direction: column;
				align-items: flex-start;
				margin-left: 20rpx;
			}
		}
		.top-card {
			width: 100%;
			border-radius: 10rpx;
			padding: 20rpx;
			box-sizing: border-box;
			background-color: #fff;
			.top {
				@extend .flex;
				justify-content: space-between;
				padding: 10rpx 15rpx;
				padding-bottom: 20rpx;
				border-bottom: 2rpx solid #f5f5f5;
			}
			.bom {
				@extend .flex;
				justify-content: space-between;
				padding: 10rpx 15rpx;
				padding-top: 20rpx;
				&-item {
					@extend .flex;
					flex-direction: column;
					text {
						margin-top: 10rpx;
						color: #333333;
						font-size: 24rpx;
					}
				}
				.btn-reset {
					padding: 0 !important;
					line-height: inherit !important;
					background: #fff !important;
					&::after {
						border: none;
					}
				}
			}
		}
		.m-list {
			margin-top: 30rpx;
		}
	}
	.mine {
		width: 100%;
		height: calc(100vh - 100rpx);
		position: absolute;
		z-index: 3;
		// background: url('../../static/images/b2.webp') no-repeat;
		// opacity: 0.7;
		@extend .flex;
		flex-direction: column;
		.logo {
			width: 200rpx;
			height: 200rpx;
			border-radius: 20rpx;
			margin-bottom: 30rpx;
		}
		.title {
			font-size: 80rpx;
			letter-spacing: 30rpx;
			font-weight: 600;
			font-family: PingFangSC-Regular, PingFang SC;
			color: #13c2c2;
		}
		.decs {
			color: #13c2c2;
			position: relative;
			// &::after{
			// 	content: '----';
			// 	position: absolute;
			// 	right: 0;
			// 	top: 50%;
			// 	margin-top: -50%;
			// }
		}
		.uni-btn-v {
			margin-top: 48rpx;
			// background-color: #fff;
			button {
				width: 702rpx;
				height: 88rpx;
				background: #13c2c2;
				box-shadow: 0px 10rpx 20rpx 0px rgba(0, 0, 0, 0.1);
				border-radius: 9rpx;
				font-size: 32rpx;
				font-family: PingFangSC-Semibold, PingFang SC;
				font-weight: 600;
				line-height: 88rpx;
				color: #ffffff;
			}
		}
	}
}
</style>
